.titulo {
  text-align: center;
  background-color: yellow;
  font-size: 60px;
}

.img{
  justify-content: center;
  display:flex;
  align-items: center;
  height: 200px;
  text-align: center;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
}


.img2{
  justify-content: center;
  display:block;
  align-items: center;
  height: 300px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
